<template>
     <div class="history-wrap" v-if="orderHist.length > 0">
         <table class="order-history">
             <tr>
                 <th>商品名</th>
                 <th>単価</th>
                 <th>注文数</th>
                 <th>注文時間</th>
             </tr>
             <tr v-for="(order, value) in orderHist" :key="value">
                 <td>{{ order.name }}</td>
                 <td>¥{{ order.price }}</td>
                 <td>{{ order.amount }}</td>
                 <td>{{ order.time }}</td>
             </tr>
         </table>
         <p class="total-price">合計金額 : ¥{{ totalPrice }}</p>
         <RouterLink to="/order/accounting">会計</RouterLink>
     </div>
     <section v-else>
         <h2 class="no-order">まだ何も注文していません</h2>
     </section>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    name:'OrderHistory',
    data() {
        return {
            orderHist: [],
            totalPrice: 0
        }
    },
    mounted() {
        this.getOrder();
    },
    methods: {
        /**
         * 注文履歴を取得する処理
         */
        getOrder() {
            const tableid = sessionStorage.getItem('table');
            if(tableid) {
                Ajax(`/api/order/history/table/${tableid}`)
                .then(res => res.json())
                .then(dat => {
                    this.orderHist = dat;
                    this.orderHist.forEach(data => {
                        this.totalPrice += data.price * data.amount;
                    });
                });
            }
        }
    },
    props: {
        orders: Object
    }
}
</script>

<style>
.order-history {
    margin: 0 auto;
}
</style>
